* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

.app {
  min-height: 100vh;
  transition: background 0.3s, color 0.3s;
}

.app.dark {
  background: #020617;
  color: #e5e7eb;
}

.app.light {
  background: #f8fafc;
  color: #020617;
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  border: none;
  border-radius: 50%;
  padding: 12px;
  font-size: 18px;
  cursor: pointer;
  background: #2563eb;
  color: white;
}

.hero {
  text-align: center;
  padding: 120px 20px;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 12px;
}

.hero p {
  opacity: 0.8;
}

.section {
  max-width: 900px;
  margin: auto;
  padding: 80px 20px;
}

.section.alt {
  background: rgba(255, 255, 255, 0.03);
}

.card {
  background: rgba(15, 23, 42, 0.9);
  padding: 24px;
  border-radius: 12px;
  margin-bottom: 20px;
  border-left: 4px solid #3b82f6;
}

footer {
  text-align: center;
  padding: 30px;
  opacity: 0.6;
  font-size: 14px;
}
